import React from 'react';
import { GlTopNav } from './GlTopNav';

export const AqMainHeader = (props) => {
  const article = props.articulos
  return (
    <header className={props.clase}>
      <GlTopNav />
      <article className="headGroupAq">
        <section>
          <article className=" flex">
            <hgroup>
              <h1>{article.title_01}</h1>
              <h5 className="darkblue"><span></span>NEW STANDART</h5>
              <h1 className="monthAq">35 <span></span><span>/ MONTH</span></h1>
            </hgroup>
          </article>
        </section>
      </article>
    </header>
  );
}